import React from 'react';
import './Cart.css';

function Cart({src}) {
    return (
        <div>
            <div className="card card_container" style={{width: "18rem"}}>
                <img src={src} className="card-img-top card_img" alt="project image"/>
                    <div className="card-body card_body_container">
                        <p className="card-text card_text_container">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
                    </div>
                    <div>
                        <button className='btn btn-outline-warning'>Demo</button>
                        <button className='btn btn-outline-warning'>Github</button>
                        <button className='btn btn-outline-warning'>Link</button>
                    </div>
            </div>
        </div>
    )
}

export default Cart
